.App {
  text-align: center;

  #quote-box {
    padding: 25px;
    background-color: white;
    border-radius: 10px;
    color: black;
    width: 40%;

    #author {
      text-align: end;
    }

    .button {
      display: flex;
      justify-content: space-between;
      min-height: 50px;

      #tweet-quote {
        display: flex;
        padding: 10px;
        color: white;
        align-self: center;
        justify-content: center;
      }
    }

    #new-quote {
      color: white;
      border-radius: 10px;
      padding: 10px;
      margin: 10px;
      border: none;
      cursor: pointer;
      font-size: 16px;
    }
  }

  .App-header {
    background-color: #6d366b9e;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
}
